<template><section>
<h1>switch 案例</h1>
<hr>
<h3>基础用法</h3>
<div class="demo-block">
    <in-switch v-model="show1"></in-switch> {{show1}}
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-switch v-model="show1"></in-switch>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">in-switch</span>  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"show1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-switch</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show1</span>: <span class="hljs-literal">false</span>
      }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<div class="demo-block">
    <in-switch disabled v-model="show2"></in-switch>
    <in-switch disabled v-model="show3"></in-switch>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-switch disabled v-model="show2"></in-switch>
<in-switch disabled v-model="show3"></in-switch>
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">in-switch</span>  <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"show2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-switch</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">in-switch</span>  <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"show3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-switch</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>拓展value类型</h3>
<div class="demo-block">
    <in-switch v-model="show4" active-value="100" inactive-value="0">
    </in-switch> {{show4}}
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-switch v-model="show4" active-value="100" inactive-value="0">
</in-switch>


</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">in-switch</span>  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"show4"</span>
            <span class="hljs-attr">active-value</span>=<span class="hljs-string">"100"</span>
            <span class="hljs-attr">inactive-value</span>=<span class="hljs-string">"0"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">in-switch</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show4</span>: <span class="hljs-string">'100'</span>
      }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>booblean</td>
<td>true/false</td>
<td>false</td>
</tr>
<tr>
<td>active-value</td>
<td>switch拓展的开值</td>
<td>string/number/boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>inactive-value</td>
<td>switch拓展的关值</td>
<td>string/number/boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            show1:false,
            show2:true,
            show3:false,
            show4:'100'
        }
    }
}
</script>


